import React from "react";

const PageHeader = ({ className, isMobile }) => {
  return (
    <section className={className}>
      <div className="content">
        <h1 className="title">
          {isMobile ? (
            "Flexible pricing that changes with your requirement."
          ) : (
            <>
              <span>Transparent Plans,</span>
              <span>Stellar Performance</span>
            </>
          )}
        </h1>
        <ul className="features">
          <li>No credit card required</li>
          <li>No coding needed</li>
        </ul>
        <ul className="features">
          <li>Assisted setup</li>
          <li>Easy plug and play</li>
        </ul>
      </div>
    </section>
  );
};

export default PageHeader;
